<template>
  <div class="main">
    <div style="line-height: 44px;">
      <div class="form-group" style="display: flex;">
        <label for="height" style="width:120px;">身高 & 体重： </label>
        <input type="number" v-model="height" id="height" style="width: 60px" placeholder="00">
        <span class="unit" style="padding-right: 16px">cm</span> 
        <input type="number" v-model="weight" id="weight" style="width: 60px" placeholder="00">
        <span class="unit" style="padding-right: 16px">kg</span> 
        <span>BMI为{{ BMI }}</span>
      </div>
      <div class="form-group" style="display: flex;">
        <label for="abdominalGirth" style="width:120px;">腹围： </label>
        <input type="number" v-model="abdominalGirth" id="abdominalGirth" style="width: 60px">
        <span class="unit">cm(厘米)</span>
      </div>
      <div class="form-group" style="display: flex;">
        <div style="display: flex;">
          <label for="symptom" style="width:120px;">症状：</label>
          <div>
            <div style="display: flex; flex-direction: row;">
              <div style="padding-right: 16px">
                <input type="checkbox" v-model="symptom" id="symptom1" value="胸痛" @click="symptomConfig">
                <label for="symptom1">胸痛</label>
              </div>
              <div style="padding-right: 16px">
                <input type="checkbox" v-model="symptom" id="symptom2" value="呼吸困难" @click="symptomConfig">
                <label for="symptom2">呼吸困难</label>
              </div>
              <div style="padding-right: 16px">
                <input type="checkbox" v-model="symptom" id="symptom3" value="其他症状" @click="symptomConfig">
                <label for="symptom3">其他症状</label>
              </div>
              <div>
                <input type="checkbox" v-model="symptomNone" id="symptom4" value="无上述症状" @click="symptomSpecial">
                <label for="symptom4">无上述症状</label>
              </div>
            </div>
            <div style="display: flex;flex-direction: row;" v-if="symptom.indexOf('胸痛') !== -1">
              <label for="">1. 胸痛是否通常是因为运动或情绪激动触发</label>
              <div>
                <input type="radio" class="btn" v-model="trigger" id="trigger1" value="是">
                <label for="trigger1">是</label>
                <input type="radio" class="btn" v-model="trigger" id="trigger2" value="否">
                <label for="trigger2">否</label>
              </div>
            </div>
            <div style="display: flex;flex-direction: row;" v-if="symptom.indexOf('胸痛') !== -1">
              <label for="">2. 胸痛是否会因为服用硝酸甘油有所缓解</label>
              <div>
                <input type="radio" class="btn" v-model="remission" id="remission1" value="是">
                <label for="remission1">是</label>
                <input type="radio" class="btn" v-model="remission" id="remission2" value="否">
                <label for="remission2">否</label>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div style="text-align: right; padding: 16px;" v-if="isnew">
      <span class="button button-blue" @click="commit"><span>添加</span></span>
    </div>
    <div style="text-align: right; padding: 16px;" v-if="!isnew">
      <span class="button button-blue" @click="updateRecord"><span>更新</span></span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'temp1',
  props: {
    mdid: String,
    templateid: String,
    recordid: String,
    isnew: Boolean
  },
  data () {
    return {
      height: '',
      weight: '',
      BMI: '',
      abdominalGirth: '',   // 腹围
      symptom: [],        // 症状
      symptomNone: false,    // 症状 --> 什么都没有
      trigger: '',        // 胸痛触发
      remission: ''       // 胸痛缓解
    }
  },
  mounted () {
    if (!this.isnew) {
      this.getRecord()
    }
  },
  watch: {
    height (val, oldVal) {
      let bmi = this.weight / ((val / 100) * (val / 100))
      this.BMI = bmi.toFixed(2)
    },
    weight (val, oldVal) {
      let bmi = val / ((this.height / 100) * (this.height / 100))
      this.BMI = bmi.toFixed(2)
    }
  },
  methods: {
    symptomSpecial () {
      this.symptomNone === true ? this.symptom = ['无上述症状'] : this.symptom = []
      // 清除子选项
      this.trigger = ''
      this.remission = ''
    },
    symptomConfig () {
      // 清除 symptom 中的‘无上述症状’，隐藏 symptomNone
      this.symptomNone = false
      let symptomNoneSite = this.symptom.indexOf('无上述症状')
      symptomNoneSite !== -1 ? this.symptom.splice(symptomNoneSite, 1) : ''
      // 清除子选项
      if (this.symptom.indexOf('胸痛') === -1) {
        this.trigger = ''
        this.remission = ''
      }
    },
    getRecord () {
      this.$http.post('/api/patient/getrecord', {recordid: this.recordid}).then(response => {
        console.log(response.data)
        if (response.data.code === 'succ') {
          let data = window.JSON.parse(response.data.data.u_patientdata)
          for (let s in data) {
            for (let d in this.$data) {
              s === d ? this.$data[d] = data[s] : ''
            }
          }
        }
      }, response => {
        console.log('serve err')
      })
    },
    // 提交数据
    commit () {
      let tempdata = {
        templateid: this.templateid,
        mdid: this.mdid,
        createuserid: this.$root.userid,
        patientdata: window.JSON.stringify(this.$data)
      }
      this.$emit('putsever', tempdata)
    },
    // 更新
    updateRecord () {
      let tempdata = {
        recordid: this.recordid,
        createuserid: this.$root.userid,
        patientdata: window.JSON.stringify(this.$data)
      }
      this.$http.post('/api/patient/updatepatientdata', tempdata).then(response => {
        console.log(response.data)
        this.$router.push({path: '/patient/' + this.mdid})
      }, response => {
        console.log('serve err')
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /*form design*/
  .form-group {
    padding: 30px;
    border-bottom: 1px solid #eee;
  }
  label {
    font-size: 16px;
    color: #666;
  }

  .unit {
    font-size: 14px;
    padding: 0 8px;
  }

  input[type="number"] {
    border: 1px solid #ddd;
    border-radius: 3px;
    background: #f5f8f9;
    text-align: center;
    font-size: 20px;
    height: 42px;
    font-weight: 500;
  }

  input:focus {
    box-shadow: 0 0 4px #528fcc;
    border-color: rgba(0,0,0,0);
    background: #fff;
  }

  /* Base for checkbox styling */
  [type="checkbox"]:not(:checked),
  [type="checkbox"]:checked {
    position: absolute;
    left: -9999px;
  }
  [type="checkbox"]:not(:checked) + label,
  [type="checkbox"]:checked + label {
    position: relative;
    padding-left: 25px;
    cursor: pointer;
  }

  /* checkbox aspect */
  [type="checkbox"]:not(:checked) + label:before,
  [type="checkbox"]:checked + label:before {
    content: '';
    position: absolute;
    left:0; top: 0;
    width: 14px; 
    height: 14px;
    border-radius: 2px;
  }
  /* checked mark aspect */
  [type="checkbox"]:not(:checked) + label:after,
  [type="checkbox"]:checked + label:after {
    position: absolute;
    content: '';
    top: 0;
    left: 5px;
    width: 2px;
    height: 9px;
    border-top: 2px solid transparent;
    border-left: 2px solid transparent;
    border-right: 2px solid #fff;
    border-bottom: 2px solid #fff;
    transform: rotateZ(37deg);
    transition: all .2s;
  }
  /* checked mark aspect changes */
  [type="checkbox"]:not(:checked) + label:before {
    border: 1px solid #aaa;
    background: #f8f8f8;
  }
  [type="checkbox"]:checked + label:before {
    border: 1px solid #01adff;
    background: #01adff;
  }
  [type="checkbox"]:not(:checked) + label:after {
    opacity: 0;
    transform: rotateZ(0deg);
  }
  [type="checkbox"]:checked + label:after {
    opacity: 1;
    transform: rotateZ(37deg);
  }
  /* disabled checkbox */
  [type="checkbox"]:disabled:not(:checked) + label:before,
  [type="checkbox"]:disabled:checked + label:before {
    box-shadow: none;
    border-color: #bbb;
    background-color: #ddd;
  }
  [type="checkbox"]:disabled:checked + label:after {
    color: #999;
  }
  [type="checkbox"]:disabled + label {
    color: #aaa;
  }

  /* radio style */
  [type="radio"] {
    position: absolute;
    left: -9999px;
  }

  [type="radio"] + label {
    position: relative;
    cursor: pointer;
    padding-left: 25px;
  }

  [type="radio"] + label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 14px;
    height: 14px;
    border-radius: 14px;
    display: inline-block;
    border: 1px solid #aaa;
    transition: .28s ease
  }

  [type="radio"]:not(:checked) + label:after,
  [type="radio"]:checked + label:after {
    content: '';
    position: absolute;
    left: 3px;
    top: 3px;
    width: 10px;
    height: 10px;
    border-radius: 10px;
    transform: scale(1.02);
    transition: all .2s;
  }

  [type="radio"]:checked + label:before {
    border-color: #01adff;
  }

  [type="radio"]:not(:checked) + label:after {
    -webkit-transform: scale(0);
    transform: scale(0)
  }
  [type="radio"]:checked + label:after {
    background: #01adff;
    -webkit-transform: scale(1.02);
    transform: scale(1.02);
  }
</style>



